  padding: 2px;
  font-size: 12px;
  font-style: italic;
  background: #000;
}

.product-img {
  min-height: 100px;
}

.jumbotron h2 {
  margin-top: -20px;
}

.name {
  background: indigo;
  color: #fff;
  font-weight: bold;
}

.black-10 {
  background: #f2f2f2;
}

.black-9 {
  background: #e6e6e6;
}

.black-8 {
  background: #d9d9d9;
}

